body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.Palette {
    display: flex;
    flex-direction: column;
    overflow-y: auto;
    height: 400px;
    width: 400px;
}


.paletteColor {
    display: flex;
    flex-direction: row;
    align-items: center;
    min-height: 2em;
}

.paletteColor p {
    margin-right: 1em;
    margin-left: 1em;
    margin-top: 0em;
    margin-bottom: 0em;
}
.PicPalette {
    display:flex;
    flex-wrap: nowrap;
    justify-content: center;
}

